import {ElMessageBox} from "element-plus";

export async function confirmDialog(message, title = '', type = 'warning') {
    try {
        const confirmResult = await ElMessageBox.confirm(message, title, {
            confirmButtonText: '',
            cancelButtonText: '',
            type: type,
        });
        return confirmResult === 'confirm'; //  true false 
    } catch (error) {
        return false; //  false
    }
}
