<template>
  <div class="user-page" v-loading="loading">
    <el-card class="user-info-card">
      <div class="clearfix">
        <span></span>
      </div>
      <el-descriptions :column="1" border>
        <el-descriptions-item label="">
          <el-button @click="handleUpdateUsername" type="success" link style="margin-left: 0">{{ username }}</el-button>
        </el-descriptions-item>
        <el-descriptions-item label="">{{ tenantInfo.name }}</el-descriptions-item>
        <el-descriptions-item label="homeRegionKey">{{ tenantInfo.homeRegionKey }}</el-descriptions-item>
        <el-descriptions-item label="upiIdcsCompatibilityLayerEndpoint">{{
            tenantInfo.upiIdcsCompatibilityLayerEndpoint
          }}
        </el-descriptions-item>
        <el-descriptions-item label="ID">{{ tenantInfo.id }}</el-descriptions-item>
        <el-descriptions-item label="">{{ tenantInfo.description }}</el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag v-for="region in tenantInfo.regions" :key="region" style="margin-right: 10px">{{ region }}</el-tag>
        </el-descriptions-item>
      </el-descriptions>
    </el-card>

    <el-card class="user-list-card">
      <div class="clearfix">
        <span></span>
      </div>
      <div style="overflow-x: auto;">
        <el-table
            :data="tenantInfo.userList"
            style="width: 100%; min-width: 800px;"
        >
          <el-table-column type="selection" width="35"/>
          <el-table-column fixed label="">
            <template #default="{ row }">
              <el-tooltip :content="row.name" placement="top">
                {{ row.name }}
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template #default="{ row }">
              <el-tooltip :content="row.email" placement="top">
                <el-tag type="primary">
                  {{ row.email }}
                </el-tag>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template #default="{ row }">
              <el-tag :type="row.lifecycleState === 'ACTIVE' ?  'success' : 'info' ">
                {{ row.lifecycleState }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template #default="{ row }">
              <el-tag :type="row.emailVerified ?  'success' : 'danger' ">
                {{ row.emailVerified ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="MFA">
            <template #default="{ row }">
              <el-tag :type="row.isMfaActivated ?  'success' : 'info' ">
                {{ row.isMfaActivated ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="timeCreated" label=""></el-table-column>
          <el-table-column prop="lastSuccessfulLoginTime" label=""></el-table-column>

          <el-table-column fixed="right" label="" width="150">
            <template #default="{ row }">
              <el-button @click="handleEdit(row)" type="primary" link>
                
              </el-button>
              <el-button @click="deleteUser(row)" type="danger" link>
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>


  <el-dialog :title="''+UpdateUserName+ ' '" v-model="showEdit"
             @close="showEdit = false">
    <el-button type="primary" @click="jsonDialogVisible = true"> JSON </el-button>
    <el-button type="danger" @click="deleteMfaDevice" :loading="deleteMfaDeviceLoading"> MFA </el-button>
    <el-button type="danger" @click="deleteApiKey" :loading="deleteApiKeyLoading"> API Key</el-button>
    <el-button type="warning" @click="resetPassword" :loading="resetPasswordLoading"></el-button>

    <el-form label-position="top" style="margin-top: 20px;">
      <el-form-item label="">
        <el-input v-model="UpdateUserParams.email" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="dbUserName">
        <el-input v-model="UpdateUserParams.dbUserName" placeholder="dbUserName" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="UpdateUserParams.description" placeholder="" type="textarea"
                  rows="6" clearable></el-input>
      </el-form-item>
    </el-form>

    <div style="text-align: right; margin-top: 20px;">
      <el-button type="primary" @click="updateUserInfo" :loading="updateUserInfoLoading"></el-button>
    </div>

    <el-dialog title="JSON " v-model="jsonDialogVisible" @close="jsonDialogVisible = false">
      <pre style="white-space: pre-wrap;">{{ jsonData }}</pre>
    </el-dialog>
  </el-dialog>

  <el-dialog
      v-model="updateCfgDialog"
      title=""
      width="30%"
      @close="updateCfgDialog = false"
  >
    <el-input v-model="updateCfgParams.updateCfgName" placeholder="" clearable/>
    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="updateCfgDialog = false">
          
        </el-button>
        <el-button type="primary" @click="updateOciCfgName()" :loading="updateCfgLoading">
          
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage} from "element-plus";
import {confirmDialog} from '@/js/common.js'

export default {
  data() {
    return {
      updateCfgParams:{
        cfgId:'',
        updateCfgName: '',
      },
      updateCfgDialog: false,
      updateCfgLoading: false,
      loading: true,
      deleteMfaDeviceLoading: false,
      deleteApiKeyLoading: false,
      resetPasswordLoading: false,
      updateUserInfoLoading: false,
      showEdit: false,
      username: '',
      ociCfgId: '',
      total: 0,
      GetTenantUserListPageParams: {
        ociCfgId: '',
        region: '',
        cleanReLaunch: false
      },
      tenantInfo: {
        id: 'xxxxxxxxxxxx',
        name: 'yohann',
        description: 'xxxx',
        homeRegionKey: 'homeRegionKey',
        upiIdcsCompatibilityLayerEndpoint: 'upiIdcsCompatibilityLayerEndpoint',
        regions: ['tag1', 'tag2', 'tag3'],
        userList: []
      },
      UpdateUserId: '',
      UpdateUserName: '',
      jsonDialogVisible: false,
      jsonData: "",
      UpdateUserParams: {
        ociCfgId: '',
        userId: '',
        email: '',
        dbUserName: '',
        description: '',
      }
    };
  },
  mounted() {
    this.ociCfgId = this.$route.query.cfgId;
    this.username = this.$route.query.username;
    this.GetTenantUserListPageParams.ociCfgId = this.ociCfgId
    this.getTenantInfo()
  },
  methods: {
    reloadTenantInfo() {
      this.GetTenantUserListPageParams.cleanReLaunch = true
      this.loading = true
      this.getTenantInfo()
      this.GetTenantUserListPageParams.cleanReLaunch = false
    },
    async getTenantInfo() {
      this.loading = true
      this.GetTenantUserListPageParams.ociCfgId = this.ociCfgId
      await this.$axios.post('/tenant/tenantInfo', {...this.GetTenantUserListPageParams})
          .then(response => {
            if (response.data.success) {
              this.tenantInfo = response.data.data;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.loading = false
    },
    handleUpdateUsername(){
      this.updateCfgDialog = true
      this.updateCfgParams.cfgId = this.ociCfgId
      this.updateCfgParams.updateCfgName = this.username
    },
    async updateOciCfgName() {
      const confirmed = await confirmDialog('', '');
      if (!confirmed) {
        this.$message.info('');
        return;
      }

      this.updateCfgLoading = true
      await this.$axios.post('/oci/updateCfgName', {...this.updateCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '',
                type: 'success',
                duration: 2000 // 
              });
              this.username = this.updateCfgParams.updateCfgName
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
            this.updateCfgLoading = false
            this.updateCfgDialog = false
            this.reloadTenantInfo();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleEdit(row) {
      this.UpdateUserId = row.id
      this.UpdateUserName = row.name
      this.jsonData = row.jsonStr
      this.UpdateUserParams.email = row.email
      this.UpdateUserParams.dbUserName = row.email
      this.UpdateUserParams.description = row.description
      this.showEdit = true
    },
    async deleteUser(row) {
      const confirmed = window.confirm('');
      if (!confirmed) {
        this.$message.info('');
        return
      }

      await this.$axios.post('/tenant/deleteUser', {
        ociCfgId: this.ociCfgId,
        userId: row.id,
      }).then(response => {
        if (response.data.success) {
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 
          });
        } else {
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 
          });
        }
      }).catch(error => {
        console.error('Error:', error)
      }).finally(() => {
        this.reloadTenantInfo()
      })
    },
    async deleteMfaDevice() {
      const confirmed = window.confirm(' MFA ');
      if (!confirmed) {
        this.$message.info('');
        return
      }

      this.deleteMfaDeviceLoading = true
      await this.$axios.post('/tenant/deleteMfaDevice', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.deleteMfaDeviceLoading = false
    },
    async deleteApiKey() {
      const confirmed = window.confirm(' API Key');
      if (!confirmed) {
        this.$message.info('');
        return
      }

      this.deleteApiKeyLoading = true
      await this.$axios.post('/tenant/deleteApiKey', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.deleteApiKeyLoading = false
    },
    async resetPassword() {
      const confirmed = window.confirm('');
      if (!confirmed) {
        this.$message.info('');
        return
      }

      this.resetPasswordLoading = true
      await this.$axios.post('/tenant/resetPassword', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.resetPasswordLoading = false
    },
    async updateUserInfo() {
      const confirmed = window.confirm('');
      if (!confirmed) {
        this.$message.info('');
        return
      }

      this.updateUserInfoLoading = true
      this.UpdateUserParams.ociCfgId = this.ociCfgId
      this.UpdateUserParams.userId = this.UpdateUserId
      await this.$axios.post('/tenant/updateUserInfo', {...this.UpdateUserParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.updateUserInfoLoading = false
    },
  },
};
</script>

<style scoped>
.el-descriptions-item {
  display: flex; /*  flexbox  */
  align-items: center; /*  */
}

.el-descriptions-item__label {
  width: 200px; /*  */
  text-align: left; /*  */
  margin-right: 10px; /*  */
}

.clearfix {
  margin-bottom: 10px;
}

.user-page {
  padding: 20px;
}

.user-info-card,
.user-list-card {
  margin-bottom: 20px;
}
</style>