<template>
  <div class="dashboard">
    <!--  -->
    <div class="controls">
      <el-select v-model="selectedRegion" placeholder="" style="width: 180px;" @change="fetchInstances">
        <el-option v-for="item in regionOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-select v-model="selectedInstance" placeholder="" style="width: 180px;" @change="fetchVnics"
                 :disabled="!selectedRegion">
        <el-option v-for="item in instanceOptions" :key="item.value" :label="item.label"
                   :value="item.value"></el-option>
      </el-select>

      <el-select v-model="selectedVnic" placeholder="vnic" style="width: 180px;" :disabled="!selectedInstance">
        <el-option v-for="item in vnicOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-select v-model="timeInterval" placeholder="" style="width: 120px;">
        <el-option v-for="item in timeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-date-picker v-model="QueryParams.beginTime" type="datetime" placeholder=""
                      style="width: 180px;"></el-date-picker>
      <el-date-picker v-model="QueryParams.endTime" type="datetime" placeholder=""
                      style="width: 180px;"></el-date-picker>
      <el-button type="primary" @click="fetchData()" :loading="loading"></el-button>
    </div>

    <!--  -->
    <div class="main-content">
      <!--  -->
      <el-card class="info-panel" shadow="hover">
        <el-descriptions
            title=""
            border
            column="1"
            size="small"
        >
          <el-descriptions-item label="">{{ currentConfig }}</el-descriptions-item>
          <el-descriptions-item label="">{{ selectedRegion }}</el-descriptions-item>
          <el-descriptions-item label="">{{ instanceCount }}</el-descriptions-item>
          <el-descriptions-item label="">{{ inboundTraffic }}</el-descriptions-item>
          <el-descriptions-item label="">{{ outboundTraffic }}</el-descriptions-item>
        </el-descriptions>
      </el-card>

      <!--  -->
      <div class="chart-container">
        <div ref="chartRef" style="width: 100%; height: 400px;"></div>
      </div>
    </div>
  </div>
</template>


<script>
import * as echarts from "echarts";
import {ElMessage} from "element-plus";

export default {
  props: {
    ociCfgId: String,
    username: String,
  },
  mounted() {
    this.currentConfig = this.username
    this.updateChart(this.data)
    this.getCondition()
  },
  data() {
    return {
      loading: true,
      selectedRegion: "",
      selectedInstance: "",
      selectedVnic: "",
      timeInterval: "",
      startTime: null,
      endTime: null,
      QueryParams: {
        ociCfgId: this.ociCfgId,
        beginTime: null,
        endTime: null,
        region: '',
        inQuery: '',
        outQuery: '',
        namespace: "oci_vcn"
      },
      regionOptions: [],
      instanceOptions: [
        {value: "instance-1", label: "1"},
        {value: "instance-1", label: "2"},
      ],
      vnicOptions: [
        {value: "vnic1", label: "vnic1"},
        {value: "vnic2", label: "vnic2"},
      ],
      timeOptions: [
        {value: "[1m]", label: ""},
        {value: "[60m]", label: ""},
        {value: "[1440m]", label: ""},
      ],
      currentConfig: "",
      instanceCount: 0,
      inboundTraffic: "0 MB",
      outboundTraffic: "0 MB",
      data: {
        time: [],
        inbound: [],
        outbound: [],
      }

    };
  },
  methods: {
    async getCondition() {
      await this.$axios.post("/traffic/getCondition", new URLSearchParams({ociCfgId: this.ociCfgId}))
          .then((response) => {
            if (response.data.success) {
              const resData = response.data.data
              this.regionOptions = resData.regionOptions
              this.selectedRegion = resData.regionOptions[0].value
              this.instanceOptions = resData.instanceOptions[this.selectedRegion] || [];
              this.fetchInstances()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: ":" + error,
              type: 'error',
              duration: 2000 // 
            });
          });
    },
    async fetchData() {
      this.loading = true
      await this.fetchInstances()

      this.QueryParams.inQuery = "VnicToNetworkBytes" + this.timeInterval + "{resourceId = \"" + this.selectedVnic + "\"}.sum()"
      this.QueryParams.outQuery = "VnicFromNetworkBytes" + this.timeInterval + "{resourceId = \"" + this.selectedVnic + "\"}.sum()"
      this.QueryParams.region = this.selectedRegion
      await this.$axios.post("/traffic/data", {...this.QueryParams})
          .then((response) => {
            if (response.data.success) {
              this.data = response.data.data
              this.updateChart(this.data);
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: ":" + error,
              type: 'error',
              duration: 2000 // 
            });
          })
          .finally(() => {
            this.loading = false
          })
    },
    // 
    async fetchInstances() {
      this.loading = true

      if (!this.selectedRegion) return;

      await this.$axios.post("/traffic/fetchInstances", new URLSearchParams({
        ociCfgId: this.ociCfgId,
        region: this.selectedRegion
      })).then((response) => {
        if (response.data.success) {
          const resData = response.data.data
          this.instanceCount = resData.instanceCount
          this.inboundTraffic = resData.inboundTraffic
          this.outboundTraffic = resData.outboundTraffic
        } else {
          ElMessage({
            message: "",
            type: 'error',
            duration: 2000 // 
          });
        }
      }).catch((error) => {
        ElMessage({
          message: ":" + error,
          type: 'error',
          duration: 2000 // 
        });
      });
      this.loading = false
    },

    //  VNIC
    async fetchVnics() {
      this.loading = true
      this.selectedVnic = ""; //  VNIC 
      this.vnicOptions = [];

      if (!this.selectedInstance) return;

      await this.$axios.post("/traffic/fetchVnics", new URLSearchParams({
        ociCfgId: this.ociCfgId,
        region: this.selectedRegion,
        instanceId: this.selectedInstance
      })).then((response) => {
        if (response.data.success) {
          this.vnicOptions = response.data.data
        } else {
          ElMessage({
            message: "vnic:",
            type: 'error',
            duration: 2000 // 
          });
        }
      }).catch((error) => {
        ElMessage({
          message: "vnic:" + error,
          type: 'error',
          duration: 2000 // 
        });
      });
      this.loading = false
    },
    updateChart(data) {
      const chartInstance = echarts.init(this.$refs.chartRef);
      const option = {
        tooltip: {
          trigger: "axis",
        },
        legend: {
          orient: "horizontal", // 
          bottom: 10,           // 
          align: "auto",        // 
          data: ["", ""],
        },
        xAxis: {
          type: "category",
          data: data.time,
          axisLabel: {
            rotate: 15 // X45
          }
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: "{value} MB" // Y MB
          }
        },
        series: [
          {
            name: "",
            type: "bar",
            data: data.inbound,
            itemStyle: {color: "#5470c6"},
          },
          {
            name: "",
            type: "bar",
            data: data.outbound,
            itemStyle: {color: "#91cc75"},
          },
        ],
      };
      chartInstance.setOption(option);
    },
  },
};
</script>

<style scoped>
.dashboard {
  padding: 20px;
}

.controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.main-content {
  display: flex; /*  Flexbox  */
}

.info-panel {
  height: auto; /*  */
  max-width: 400px;
  margin: 20px auto;
  //background-color: #ffffff;
  border-radius: 20px;
  padding: 10px;
}

.chart-container {
  flex-grow: 1; /*  */
  margin-left: 20px; /*  */
}
</style>

